import React from "react";
import "./scss/Navbar.css";
import logo from "./../assets/CHLogoTxtWhite.svg";

function Navbar() {
  return (
    <>
      <nav>
        <a href="" className="navbar-brand">
          <img src={logo} alt="Code Heist Logo" />
        </a>
        
      </nav>
    </>
  );
}

export default Navbar;
